:root {
  --font-body: "Open Sans", Arial, sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font-family: var(--font-body);
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 1rem 2rem;
  background-color: #121212;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-transform: uppercase;
  font-weight: bold;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1 {
  margin-top: 0;
  line-height: 1.1;
  letter-spacing: 0.125rem;
  font-size: 2.25rem;
}

.guess {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 0.1875rem;
  width: 18.75rem;
}
.guess__tile {
  border: 0.125rem solid #434343;
  width: 3.25rem;
  height: 3.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  font-weight: 600;
}